import \"components/board\";\n@import \"components/gameinfo\";",":root {\n    // Simple fonts\n    --font-family-sans: 'Poppins', 'Rust', system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n\n    // Classical line heights\n    --line-height-headers: 1.1;\n    --line-height-body:    1.5;\n    --line-height-break:   2;\n\n    // Classical and robust font sizes system\n    --font-size-xs: 0.75rem;   // 12px\n    --font-size-s: 0.875rem;   // 14px\n    --font-size-m: 1rem;       // 16px\n    --font-size-l: 1.125rem;   // 18px\n    --font-size-xl: 1.25rem;   // 20px\n    --font-size-xxl: 1.5rem;   // 24px\n    --font-size-xxxl: 2rem;    // 32px\n    --font-size-xxxxl: 2.5rem; // 40px\n\n    // Three different text colors\n    --color-text-header: hsl(0, 1%, 16%);\n    --color-text-body:   hsl(0, 5%, 25%);\n    --color-text-muted:  hsl(0, 1%, 44%);\n\n    // Classical and robust spacing system\n    --space-xxxs: 0.25rem; // 4px\n    --space-xxs: 0.375rem; // 6px\n    --space-xs: 0.5rem;    // 8px\n    --space-s: 0.75rem;    // 12px\n    --space-m: 1rem;       // 16px\n    --space-l: 1.5rem;     // 24px\n    --space-xl: 2rem;      // 32px\n    --space-xxl: 2.5rem;   // 40px\n    --space-xxxl: 3rem;    // 48px\n    --space-xxxxl: 4rem;   // 64px\n\n    // Application colors\n    --color-primary:          #d6d0b8;\n    --color-primary-rotate:   rgb(23, 103, 223);\n    --color-primary-bg:       #5baaec;\n    --color-secondary:        #4c9173;\n    --color-secondary-rotate: #902424;\n    --color-tertiary:         hsl(49, 89%, 64%);\n    --color-glint:            rgb(163, 209, 255);\n\n    // Neutral colors\n    --color-white:      hsl(0, 0%, 100%);\n    --color-background: #33425b;\n    --color-light:      hsl(0, 6%, 93%);\n    --color-dark:       var(--color-text-header);\n\n    // Border radius\n    --border-radius: 0.375rem;\n\n    // Border\n    --border: solid 2px var(--color-light);\n    --border-dark: solid 2px var(--color-dark);\n\n    // Shadows\n    --shadow-large:  2px 4px 10px hsl(0 0% 0% / 0.1);\n    --shadow-small:  1px 3px 6px hsl(0 0% 0% / 0.1);\n}\n","*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n}\n\nhtml {\n  overflow-y: scroll;\n  height: 100%;\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  min-height: 100%;\n  background-image: url(../assets/images/radar02.jpg);\n  background-repeat: no-repeat; /* Prevents the image from repeating */\n  background-size: cover; /* Scales the image to cover the entire container */\n  background-position: center;\n\n  // background-color: var(--color-background);\n  color: var(--color-text-body);\n  line-height: var(--line-height-body);\n  font-family: var(--font-family-sans);\n}\n\nimg,\npicture,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  color: var(--color-light);\n  line-height: var(--line-height-headers);\n  font-family: 'Rust';\n  font-weight: 500;\n}\n\nh1 {\n  font-size: var(--font-size-xxxl);\n}\n\nh2 {\n  font-size: var(--font-size-xxl);\n}\n\nh3 {\n  font-size: var(--font-size-xl);\n}\n\nh4 {\n  font-size: var(--font-size-l);\n}\n\na {\n  color: var(--color-primary);\n  text-decoration: none;\n  transition: color 200ms;\n\n  &:hover,\n  &:focus,\n  &:active {\n    color: var(--color-primary-rotate);\n  }\n}\n\n",".container {\n    // border: var(--border-dark);\n    position: relative;\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-template-rows: 0.1fr auto 0.1fr;\n    grid-template-areas:\n      \"header\"\n      \"content\"\n      \"footer\";\n\n    width: 100%;\n    // height: 100vh;\n    // padding-right: var(--space-xs);\n    // padding-left: var(--space-xs);\n    margin-left: auto;\n    margin-right: auto;\n    // margin-top: var(--space-l);\n    \n    border-radius: var(--border-radius);\n    // box-shadow: var(--shadow-small);\n    // background-color: var(--color-primary-bg);\n  \n    // background-color: transparent;\n\n    @include media(tabletAndUp) {\n      // padding-right: var(--space-m);\n      // padding-left: var(--space-m);\n      max-width: 60rem;\n    }\n  }","@mixin media($query) {\n    @if $query == tabletAndUp {\n      @media (min-width: 50rem) { @content; }\n    }\n  }",".content {\n    grid-area: content;\n    margin: var(--space-s);\n    // border: var(--border-dark);\n    border-radius: var(--border-radius);\n\n    display: flex;\n    flex-direction: column;\n    // justify-content: space-around;\n}","footer {\n    grid-area: footer;\n    margin: var(--space-s);\n    border: var(--border-dark);\n    border-radius: var(--border-radius);\n}","header {\n    grid-area: header;\n    margin: var(--space-s);\n    // border: var(--border-dark);\n    border-radius: var(--border-radius);\n    \n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n  }",".sidebar {\n    grid-area: sidebar;\n    margin: var(--space-s);\n    border: var(--border-dark);\n    border-radius: var(--border-radius);\n    background-color: var(--color-secondary-bg);\n\n\n  }\n\n  .profile {\n    display: flex;\n    flex-direction: column;\n    .profile-title {\n      font-size: var(--font-size-xxl);\n      font-weight: bold;\n    }\n  }","// board.scss\n\n.board-container {\n    display: flex;\n    justify-content: space-between;\n    padding-top: var(--space-l);\n}\n\n.board-cell {\n    // width: 50px; // Match the width in grid-template-columns\n    // height: 50px; // Match the height in grid-template-rows\n    border: 2px solid var(--color-primary); // Border to visualize grid cells\n    border-radius: var(--border-radius);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: var(--color-background);\n    // box-shadow: var(--shadow-large);\n}\n\n.p1-board {\n    display: grid;\n    grid-template-columns: repeat(10, 35px); // Adjust the size based on your needs\n    grid-template-rows: repeat(10, 35px);\n    gap: 1px;\n    \n    .board-cell.water {\n        background-color: var(--color-primary-rotate); // Different color for 'water'\n    }\n    \n    .board-cell.ship {\n        background-color: var(--color-secondary); // Different color for ship\n    }\n}\n.p2-board {\n    // border: var(--border-dark);\n    display: grid;\n    grid-template-columns: repeat(10, 35px); // Adjust the size based on your needs\n    grid-template-rows: repeat(10, 35px);\n    gap: 1px; // Space between cells (optional)\n\n    .board-cell.water {\n        background-color: var(--color-primary-rotate);\n        // background-color: rgb(23, 103, 223); // Different color for 'water'\n    }\n    \n    .board-cell.ship {\n        background-color: var(--color-primary-rotate);\n        // background-color: rgb(23, 103, 223);// Different color for ship\n    }\n}\n\n","// _gameinfo.scss\n\n.game-info {\n    // border: var(--border-dark);\n    // color: var(--color-light);\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: center;\n    \n    .top {\n        color: var(--color-primary);\n        font-size: var(--font-size-xl);\n        font-weight: bold;\n\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        // justify-content: center; \n\n        .feedback-image {\n            display: block;\n            align-items: center;\n        }\n    }\n\n    .middle {\n        text-align: center;\n        font-size: var(--font-size-xxxxl);\n        font-weight: 700;\n        color: var(--color-secondary-rotate);\n        filter: brightness(1.2) contrast(1.5);\n        text-shadow: \n            0 0 5px rgba(255, 255, 255, 0.8), /* White glow */\n            0 0 10px rgba(255, 255, 255, 0.6), /* White glow */\n            0 0 15px rgba(255, 255, 255, 0.4); /* White glow */\n            animation: none; /* Ensure no animation is applied initially */\n        }\n        \n        .middle.fade-out {\n            animation: fadeOutAndShrink 3s forwards;\n        }\n        \n        @keyframes fadeOutAndShrink {\n            0% {\n                opacity: 1;\n                transform: scale(1);\n            }\n            100% {\n                opacity: 0;\n                transform: scale(0.5); /* Shrinks the text to 50% of its original size */\n            }\n        }\n\n    .bottom {\n        color: var(--color-light);\n    }\n}\n"],"sourceRoot":""}]);
